/*  --  Start of Global setting --  */
body{
        font-family: 'Open Sans','Rounded Mplus 1c', Segoe UI, "\30E1\30A4\30EA\30AA", Meiryo, sans-serif;
        font-weight: 300;
        color:#737373;
        background-color:#ffffff;
        margin: 0 auto;
        
        max-height: 100%;
        -webkit-text-size-adjust: 100%;
}
p {
        max-height: 100%;
        margin : 5px 0px;
}
ul{
        list-style-type: none;
        margin-left:0px;
        padding-left:0px;
        font-size : 90%;
}
ul li:before{
        content: '\3007   ';
        color: #ffc000;
        font-weight: bold;
}
li{
        margin: 5px 0px;
}
a {
        color : #00a7ce;
        text-decoration: none;
        border-bottom: 1px solid #00a7ce;
        -webkit-transition-property: all;
        transition-property: all;
        -webkit-transition: 0.2s linear;
        transition: 0.2s linear;
}
a:hover{
        color: #dcdcdc;
        border-bottom: 1px solid #dcdcdc;
}
input {
        background: #fafafa;
        border: none;
        border-bottom: 2px solid #e9e9e9;
        color: #666;
        font-family: 'Open Sans', sans-serif;
        font-size: 1em;
        height: 50px;
        -webkit-transition: border-color 0.3s;
        transition: border-color 0.3s;
        width: 100%;
}
input:focus {
        border-bottom: 2px solid #c0c0c0;
        outline: none;
}
textarea{
      background: #fafafa;
      border: none;
      border-bottom: 2px solid #e9e9e9;
      color: #666;
      font-family: 'Open Sans', sans-serif;
      font-size: 1em;
      -webkit-transition: border-color 0.3s;
      transition: border-color 0.3s;
      width: 100%;
}
textarea:focus{
      border-bottom: 2px solid #c0c0c0;
      outline: none;
}
/*  --  End of Global setting --  */
/* -- Start of Global parts setting -- */
.buttonBase {
      color: #099acb;
      border: 1px #099acb solid;
      border-radius: 5px;
      cursor: pointer;
      font-size: 1em;
      letter-spacing: 0.05em;
      text-align: center;
      text-transform: uppercase;
      -webkit-transition: background 0.3s ease-in-out;
      transition: background 0.3s ease-in-out;
      /* width: 100%; */
      padding : 5px;
      margin : 10px 0px;
}
.buttonBase:hover {
      color: #BBDEFB;
      border: 1px #BBDEFB solid;
}
/* -- End of Global parts setting -- */
/* -- Start of main content section */
section {
        width: 90%;
        max-width: 800px;
        margin: 0 auto;
        margin-top: 30px;
        margin-bottom: 30px;
        display: block;
}
article
    {
      margin: 0px;
      padding: 0px;
}
/* -- End of main content section */
/* -- Start of Map Maker Global settings --*/
.mapInfo
    {
      font-size:120%;
}
.markerTitle{
        color : #00a7ce;
        font-size : 150%;
        font-weight: bolder;
}
/* -- End of Map Maker Global settings --*/
.sidef-enter-active, .sidef-leave-active{
        -webkit-transition: opacity 0.5s, -webkit-transform 0.5s;
        transition: opacity 0.5s, -webkit-transform 0.5s;
        transition: opacity 0.5s, transform 0.5s;
        transition: opacity 0.5s, transform 0.5s, -webkit-transform 0.5s;
}
.sidef-enter{
        opacity: 0;
        -webkit-transform : translatex(-250px);
                transform : translatex(-250px);
}
.sidef-leave-to{
        opacity: 0;
        -webkit-transform : translatex(-250px);
                transform : translatex(-250px);
}
