#mnavToggle {
        width: 45px;
        height: 40px;
        cursor: pointer;
        float: left;
        margin: 3px 15px 15px 15px;
        position: absolute;
        top:10px;
        display: block;
}
#mnavToggle span {
        display: block;
        position: absolute;
        width: 100%;
        border-bottom: solid 4px #00a7ce;
}
#mnavToggle span:nth-child(1) {
        top: 0;
}
#mnavToggle span:nth-child(2) {
        top: 15px;
}
#mnavToggle span:nth-child(3) {
        top: 30px;
}
#mnavToggle div {
        position: relative;
}
